export default class Popup {
    constructor(popupSelector) {
        this.popupSelector = popupSelector;
        this.popup = popupSelector;
      }

    // Ð¾ÑÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð²
    open() {
        this.popup.classList.add('popup_open');
        document.addEventListener('keyup', this._handleEscClose);
    }

    // Ð·Ð°ÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð²
    close() {
        this.popup.classList.remove('popup_open');
        document.removeEventListener('keyup', this._handleEscClose);
    }

    // Ð·Ð°ÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð² Ð¿Ð¾ Esc
    _handleEscClose = (evt) => {
        if (evt.key === 'Escape') {
            const currentPopup = document.querySelector('.popup_open');
            this.close(currentPopup);
        }
    }

    setEventListeners() {
        // Ð·Ð°ÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð² Ð½Ð° ÐºÑÐµÑÑÐ¸Ðº
        this.popup.querySelector('.popup__close').addEventListener('click', () => this.close());
        // Ð·Ð°ÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð² Ð¿Ð¾ Ð¾Ð²ÐµÑÐ»ÐµÑ
        this.popup.addEventListener('mousedown', (evt) => {
            if (evt.target.classList.contains('popup__overlay')) {
                this.close();
            }
        });
    }
}
